import React, { Component } from 'react';
import './App.css';
import img_elImage from './images/Screen12_elImage_255308.jpg';
import img_elImage3 from './images/Screen12_elImage3_1002894.jpg';
import img_elImage2 from './images/Screen12_elImage2_403128.jpg';
import img_elImageCopy from './images/Screen12_elImage_255308.jpg';
import img_elImageCopy2 from './images/Screen12_elImage3_1002894.jpg';
import img_elImageCopy3 from './images/Screen12_elImage2_403128.jpg';
import btn_icon_905850 from './images/btn_icon_905850.png';
import btn_icon_761162 from './images/btn_icon_761162.png';

export default class Screen12 extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      elImageCopy_visible: false,
      elImageCopy2_visible: false,
      elImageCopy3_visible: false,
      textarea: '',
      textblock: (<div>Almost done!</div>),
      textblock_plainText: "Almost done!",
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  onClick_elImage = (ev) => {
    let newVal = "image";
    this.props.appActions.updateDataSlot('ds_profile pic', newVal);
  
    this.setState({elImageCopy_visible: true});
  
    this.setState({elImageCopy2_visible: false});
  
    this.setState({elImageCopy3_visible: false});
  
  }
  
  
  onClick_elImage3 = (ev) => {
    let newVal = "image 3";
    this.props.appActions.updateDataSlot('ds_profile pic', newVal);
  
    this.setState({elImageCopy2_visible: true});
  
    this.setState({elImageCopy_visible: false});
  
    this.setState({elImageCopy3_visible: false});
  
  }
  
  
  onClick_elImage2 = (ev) => {
    let newVal = "image 2";
    this.props.appActions.updateDataSlot('ds_profile pic', newVal);
  
    this.setState({elImageCopy3_visible: true});
  
    this.setState({elImageCopy_visible: false});
  
    this.setState({elImageCopy2_visible: false});
  
  }
  
  
  onClick_elImageCopy = (ev) => {
    let newVal = "image copy";
    this.props.appActions.updateDataSlot('(null)', newVal);
  
  }
  
  
  onClick_elImageCopy2 = (ev) => {
    let newVal = "image copy 2";
    this.props.appActions.updateDataSlot('(null)', newVal);
  
  }
  
  
  onClick_elImageCopy3 = (ev) => {
    let newVal = "image copy 3";
    this.props.appActions.updateDataSlot('(null)', newVal);
  
  }
  
  
  textAreaChanged_elTextarea = (event) => {
    this.setState({textarea: event.target.value});
  }
  
  onClick_elBack = (ev) => {
    // Go back in screen navigation history
    this.props.appActions.goBack();
  
  }
  
  
  onClick_elContinue = (ev) => {
    // Go to screen 'Screen 13'
    this.props.appActions.goToScreen('screen13', { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    
    const style_elRectangleCopy = {
      background: 'rgba(98, 115, 101, 1.000)',
     };
    
    const style_elHotspot = {
      display: 'block',
      textTransform: 'uppercase',
      backgroundColor: 'transparent',
     };
    const style_elImage = {
      backgroundImage: 'url('+img_elImage+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elImage3 = {
      backgroundImage: 'url('+img_elImage3+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elImage2 = {
      backgroundImage: 'url('+img_elImage2+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elImageCopy = {
      backgroundImage: 'url('+img_elImageCopy+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const elImageCopy = this.state.elImageCopy_visible ? (
      <div className="elImageCopy">
        <div style={style_elImageCopy} onClick={this.onClick_elImageCopy}  />
      </div>
      
     ) : null;
    const style_elImageCopy2 = {
      backgroundImage: 'url('+img_elImageCopy2+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const elImageCopy2 = this.state.elImageCopy2_visible ? (
      <div className="elImageCopy2">
        <div style={style_elImageCopy2} onClick={this.onClick_elImageCopy2}  />
      </div>
      
     ) : null;
    const style_elImageCopy3 = {
      backgroundImage: 'url('+img_elImageCopy3+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const elImageCopy3 = this.state.elImageCopy3_visible ? (
      <div className="elImageCopy3">
        <div style={style_elImageCopy3} onClick={this.onClick_elImageCopy3}  />
      </div>
      
     ) : null;
    
    const style_elTextarea = {
      display: 'block',
      backgroundColor: 'white',
      borderColor: 'lightGray',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      pointerEvents: 'auto',
     };
    
    const style_elBack = {
      display: 'block',
      textTransform: 'uppercase',
      backgroundImage: 'url('+btn_icon_905850+')',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '88.056%',
      backgroundPosition: '50% 0%',
      color: '(null)',
      textAlign: 'left',
      backgroundColor: 'transparent',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    const style_elContinue = {
      display: 'block',
      textTransform: 'uppercase',
      backgroundImage: 'url('+btn_icon_761162+')',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '88.603%',
      backgroundPosition: '50% 0%',
      color: '(null)',
      textAlign: 'left',
      backgroundColor: 'transparent',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elTextBlock = {
      fontSize: 24.5,
      color: 'black',
      textAlign: 'left',
     };
    const style_elTextBlock2 = {
      color: 'black',
      textAlign: 'left',
     };
    
    return (
      <div className="AppScreen Screen12" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
          
          <div className="elRectangleCopy" style={style_elRectangleCopy} />
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elHotspot">
            <div className="actionFont" style={style_elHotspot} />
          </div>
          
          <div className="elImage">
            <div style={style_elImage} onClick={this.onClick_elImage}  />
          </div>
          
          <div className="elImage3">
            <div style={style_elImage3} onClick={this.onClick_elImage3}  />
          </div>
          
          <div className="elImage2">
            <div style={style_elImage2} onClick={this.onClick_elImage2}  />
          </div>
          
          { elImageCopy }
          { elImageCopy2 }
          { elImageCopy3 }
          <div className="elTextarea">
            <textarea className="baseFont" style={style_elTextarea}  placeholder={this.props.locStrings.screen12_textarea_840931} onChange={this.textAreaChanged_elTextarea} value={this.state.textarea}  />
          </div>
          
          <div className="elBack">
            <button className="actionFont" style={style_elBack} onClick={this.onClick_elBack}  />
          </div>
          
          <div className="elContinue">
            <button className="actionFont" style={style_elContinue} onClick={this.onClick_elContinue}  />
          </div>
        </div>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <div className="systemFontRegular  elTextBlock" style={style_elTextBlock}>
              <div>{this.state.textblock}</div>
            </div>
            <div className="baseFont elTextBlock2" style={style_elTextBlock2}>
              <div>{this.props.locStrings.screen12_textblock2_106003}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
  
}
