import React, { Component } from 'react';
import './App.css';
import img_elTeaMatelogo from './images/TeaMateScreen_elTeaMatelogo_748633.png';

// UI framework component imports
import Button from 'muicss/lib/react/button';

export default class TeaMate2Screen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      emailField: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['userEmail'] : '') || '',
      passwordField: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_userpassword'] : '') || '',
      elValidator_visible: true,
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  textInputChanged_elEmailField = (event) => {
    this.setState({emailField: event.target.value});
    
    this.props.appActions.updateDataSlot("userEmail", event.target.value);
  }
  
  textInputChanged_elPasswordField = (event) => {
    this.setState({passwordField: event.target.value});
    
    this.props.appActions.updateDataSlot("ds_userpassword", event.target.value);
  }
  
  onClick_elValidator = (ev) => {
  
  }
  
  
  onClick_elButton = (ev) => {
    // Go back in screen navigation history
    this.props.appActions.goBack();
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    
    const style_elRectangle = {
      background: 'rgba(98, 115, 101, 1.000)',
     };
    
    
    const value_emailField = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['userEmail'] : '');
    
    const style_elEmailField = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      fontSize: 17.1,
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const value_passwordField = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_userpassword'] : '');
    
    const style_elPasswordField = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      fontSize: 17.1,
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    const style_elValidator = {
      color: 'black',
      textAlign: 'left',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const elValidator = this.state.elValidator_visible ? (
      <div className="elValidator">
        <div className="baseFont" style={style_elValidator} onClick={this.onClick_elValidator} >
          <div>{this.props.locStrings.teamate2_validator_468954}</div>
        </div>
      </div>
      
     ) : null;
    
    const style_elButton = {
      display: 'block',
      color: '#fff',
      textAlign: 'center',
      backgroundColor: '#1e1a1c',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    
    const style_elTextBlock = {
      color: 'black',
      textAlign: 'left',
     };
    const style_elTeaMatelogo = {
      backgroundImage: 'url('+img_elTeaMatelogo+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
     };
    
    return (
      <div className="AppScreen TeaMate2Screen" style={baseStyle}>
        <div className="background">
          <div className="elRectangle" style={style_elRectangle} />
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elSpacer">
            <div />
          </div>
          
          <div className="elTentIcon">
            <div />
          </div>
          
          <div className="elEmailField">
            <input className="font-avenirNextRegular" style={style_elEmailField} type="email" placeholder={this.props.locStrings.teamate2_emailfield_629900} onChange={this.textInputChanged_elEmailField} value={value_emailField !== undefined ? value_emailField : ''}  />
          </div>
          
          <div className="elPasswordField">
            <input className="font-avenirNextRegular" style={style_elPasswordField} type="password" placeholder={this.props.locStrings.teamate2_passwordfield_646351} onChange={this.textInputChanged_elPasswordField} value={value_passwordField !== undefined ? value_passwordField : ''}  />
          </div>
          
          { elValidator }
          <div className="elButton">
            <Button className="actionFont" style={style_elButton}  variant="raised" onClick={this.onClick_elButton} >
              {this.props.locStrings.teamate2_button_152687}
            </Button>
          </div>
        </div>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <div className="elGraphic" />
            <div className="elGraphic2" />
            <div className="baseFont elTextBlock" style={style_elTextBlock}>
              <div>{this.props.locStrings.teamate2_textblock_261425}</div>
            </div>
            <div className="elTeaMatelogo" style={style_elTeaMatelogo} />
          </div>
        </div>
      </div>
    )
  }
  
}
