import React, { Component } from 'react';
import './App.css';
import btn_icon_306561 from './images/btn_icon_306561.png';

export default class IdealTeaMateScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      text: (<div> </div>),
      text_plainText: " ",
      textcopy: (<div> </div>),
      textcopy_plainText: " ",
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  onClick_elIconButton = (ev) => {
    // Go to screen 'Screen 12'
    this.props.appActions.goToScreen('screen12', { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    
    const style_elRectangleCopy = {
      background: 'rgba(98, 115, 101, 1.000)',
     };
    
    const value_text = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_class1'] : '');
    
    const style_elText = {
      color: 'black',
      textAlign: 'left',
     };
    
    const value_textCopy = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_interest1'] : '');
    
    const style_elTextCopy = {
      color: 'black',
      textAlign: 'left',
     };
    
    const style_elIconButton = {
      display: 'block',
      textTransform: 'uppercase',
      backgroundImage: 'url('+btn_icon_306561+')',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '59.588%',
      backgroundPosition: '50% 0%',
      color: '(null)',
      textAlign: 'left',
      backgroundColor: 'transparent',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elTextBlock = {
      fontSize: 26.4,
      color: 'black',
      textAlign: 'left',
     };
    
    return (
      <div className="AppScreen IdealTeaMateScreen" style={baseStyle}>
        <div className="background">
          <div className="elRectangleCopy" style={style_elRectangleCopy} />
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elText">
            <div className="baseFont" style={style_elText}>
              <div>{value_text !== undefined ? value_text : (<span className="propValueMissing">{this.state.text}</span>)}</div>
            </div>
          </div>
          
          <div className="elTextCopy">
            <div className="baseFont" style={style_elTextCopy}>
              <div>{value_textCopy !== undefined ? value_textCopy : (<span className="propValueMissing">{this.state.textcopy}</span>)}</div>
            </div>
          </div>
          
          <div className="elIconButton">
            <button className="actionFont" style={style_elIconButton} onClick={this.onClick_elIconButton}  />
          </div>
        </div>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <div className="systemFontRegular  elTextBlock" style={style_elTextBlock}>
              <div>{this.props.locStrings.idealteamate_textblock_195554}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
  
}
